import React, {useContext, useState}from "react";
import {Link} from "react-router-dom"
import "./navbar.scss";
import SearchIcon from "@mui/icons-material/Search";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import {context} from '../../ShoppingContext';

const Navbar = () => {
  const {cart,handleSearch} = useContext(context);
  const [searchText, setSearchText] = useState("");
  
  
  const handleInput =(e) =>{
    setSearchText(e.target.value);
    handleSearch(e.target.value);
  }
  return (
    <header>
      <nav className="navbar" >
        <div className="logo icon"><Link className="icon" to={'/'}>TeeRex</Link></div>
        {!(window.location.href.includes("cart") || window.location.href.includes("orderSuccess")) && <div className="search_bar">
          <input id="search" type="search" placeholder="Search T-Shirts..." value={searchText} onChange={handleInput}/>
          <SearchIcon className="icon"/>
        </div>}
        <Link to={'/cart'} className="cart_icon_container">
          <ShoppingCartIcon fontSize="large" className="icon"/>
          <small>{cart.length}</small>
        </Link>
      </nav>
    </header>
  );
};

export default Navbar;
