import React, { useContext } from "react";
import { context } from "../../ShoppingContext";
import './error.scss'

const Error = () => {
  const { showError, setShowError } = useContext(context);
  return (
    <div className="error_modal" style={{ display: showError.status }}>
      <div className="error">
      <h3>{showError.errorMSG}</h3>
      <button
        className="btn"
        onClick={() =>
          setShowError((prevVal) => {
            return {
              ...prevVal,
              status: "none",
            };
          })
        }
      >
        X
      </button>
      </div>
    </div>
  );
};

export default Error;
