import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import "./cartItem.scss"
import {context} from '../../ShoppingContext'
import { useContext } from 'react';


const CartItem = (props) => {

  const {cart, addToCart, reduceCartQty, removeItem} = useContext(context);
  const {id, imageURL, name, price, qtyInCart} = props.item;

  return (
    <div className="cart_item">
              <img
                src={imageURL}
                alt="product image"
                className="item_img"
              />
              <div className="cart_item_details">
                <div className="left">
                  <h3>{name}</h3>
                  <p>₹ {price}</p>
                  <div className="qty_container">
                    <button className="qty_btn" onClick={()=>reduceCartQty(id)}>-</button>
                    <div className="qty"> {qtyInCart} </div>
                    <button className="qty_btn" onClick={()=>addToCart(id)}>+</button>
                    
                  </div>
                </div>
                <div className="right">
                  <button className="delete_btn" onClick={()=>removeItem(id)}><DeleteForeverIcon fontSize='large'/></button>
                </div>
              </div>
            </div>
  )
}

export default CartItem