import SummaryItem from "../summaryItem/SummaryItem";
import "./priceDetails.scss";
import DiscountIcon from "@mui/icons-material/Discount";
import { context } from "../../ShoppingContext";
import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";

const PriceDetails = () => {
  const { cart,placeOrder } = useContext(context);
  const disc = 10;
  let [delivery_charge, setDelivery_charge] = useState(0);
  const [totalAmount, setTotalAmount] = useState(0);

  useEffect(() => {
    let total = 0;

    if (cart.length) {
      setDelivery_charge(49);
      cart.forEach((item) => {
        total += item.price * item.qtyInCart;
      });
      total = (total * (100 - disc)) / 100;
      total += delivery_charge;
      setTotalAmount(total);
    }else{
      setDelivery_charge(0);
      setTotalAmount(0);
    }
  }, [cart]);

  return (
    <div className="price_details">
      <h2 className="cart_title">Price Details</h2>
      <div className="items">
        {cart.map((item) => (
          <SummaryItem item={item} />
        ))}
      </div>
      <div className="items discount">
        <div className="summary_item">
          <p>Delivery Charge</p>
          <h3 className="amount">{delivery_charge}</h3>
        </div>
        <div className="summary_item">
          <p className="disc_icon">
            Discount <DiscountIcon />
            {disc}%{" "}
          </p>
          <h3 className="amount">- {Math.round(totalAmount * (disc))/100}</h3>
        </div>
      </div>
      <div className="items total">
        <div className="summary_item">
          <h2>Total Amount</h2>
          <h3 className="amount">{totalAmount} ₹</h3>
        </div>
        <Link to={'/orderSuccess'}><button className="order_btn"
        onClick={()=> placeOrder()}>Place Order</button></Link>
      </div>
    </div>
  );
};

export default PriceDetails;
