import React, { createContext, useEffect, useState } from "react";

const context = createContext();

const ShoppingContext = (props) => {
  // variables and state
  const url =
    "https://geektrust.s3.ap-southeast-1.amazonaws.com/coding-problems/shopping-cart/catalogue.json";
  const [data, setData] = useState([]);
  const [sessionData, setSessionData] = useState([]);
  const [cart, setCart] = useState([]);
  const [dataToDisplay, setDataToDisplay] = useState(data);
  const [searchedData, setSearchedData] = useState([]);
  const [filters, setFilters] = useState({
    color: [],
    type: [],
    gender: [],
    priceRange: [0, 10000],
  });
  const [colors, setColors] = useState([]);
  const [genders, setGenders] = useState([]);
  const [types, setTypes] = useState([]);
  const [isFilterReset, setIsFilterReset] = useState(false);
  const [showError, setShowError] = useState({
    status: "none",
    errorMSG: "",
  });

  //   fetch and set data
  const fetchData = async () => {
    const response = await fetch(url);
    const data = await response.json();
    setData(data);
    setSessionData(data);
    setSearchedData(data);
    setColors(Array.from(new Set(data.map((item) => item.color))));
    setGenders(Array.from(new Set(data.map((item) => item.gender))));
    setTypes(Array.from(new Set(data.map((item) => item.type))));
  };
  useEffect(() => {
    fetchData();
  }, []);

  // useEffect(() => {
  //   setDataToDisplay(data);
  // }, [data]);

  useEffect(() => {
    setDataToDisplay(searchedData);
  }, [sessionData]);

  // filter items based on filters selected
  useEffect(() => {
    // reset filter functionality - retained search results, only filters are removed
    if (isFilterReset) {
      setDataToDisplay(searchedData);
      console.log(filters);
    } else {
      // refine search results using filters chosen
      let tempData = searchedData.filter((item) => {
        return (
          (filters.gender.length
            ? filters.gender.includes(item.gender)
            : true) &&
          (filters.color.length ? filters.color.includes(item.color) : true) &&
          (filters.type.length ? filters.type.includes(item.type) : true) &&
          (item.price >= filters.priceRange[0] && item.price <= filters.priceRange[1])
        );
      });
      setDataToDisplay(tempData);
      console.log(filters);
    }
  }, [filters]);

  // search T-shirts using free text (any of name, color, type)==============
  const handleSearch = (val) => {
    val = val.toLowerCase();
    let tempData = sessionData.filter((item) => {
      return (
        item["name"].toLowerCase().includes(val) ||
        item["color"].toLowerCase().includes(val) ||
        item["type"].toLowerCase().includes(val)
      );
    });
    setDataToDisplay(tempData);
    setSearchedData(tempData);
  };

  //   add to cart functionality ===============================================
  const addToCart = (id) => {
    let index = sessionData.findIndex((item) => item.id === id);
    if (sessionData[index].quantity > 0) {
      // find if the item is already present in cart,
      let cartIndex = cart.findIndex((item) => item.id === id); //will return -1 if item is not in cart

      // if item already in cart then increase qtyInCart , else add new item with qtyInCart=1
      if (cartIndex === -1) {
        setCart((prevValue) => [
          ...prevValue,
          { ...sessionData[index], qtyInCart: 1 },
        ]);
      } else {
        let tempCart = [...cart];
        tempCart[cartIndex] = {
          ...tempCart[cartIndex],
          qtyInCart: tempCart[cartIndex].qtyInCart + 1,
        };
        setCart(tempCart);
      }

      //  update qty in database
      setSessionData((prevValue) => {
        prevValue[index].quantity = prevValue[index].quantity - 1;
        return [...prevValue];
      });

      blow();
    } 
    
    else{
      setShowError((prevVal) => {
        return {
          ...prevVal,
          status: "block",
          errorMSG: `You have reached maximum available quantity for ${sessionData[index].name}`,
        };
      });}
  };

  // reduce cart qty ===============================================================
  const reduceCartQty = (id) => {
    // find if the item is already present in cart,
    let cartIndex = cart.findIndex((item) => item.id === id); //will return -1 if item is not in cart

    // if item already in cart then =>
    //          check qtyInCart is more than 1,
    //                  if yes => reduce qtyInCart,
    //          else => show errror that Minimum qty required is 1

    if (cartIndex !== -1) {
      let tempCart = [...cart];

      if (tempCart[cartIndex].qtyInCart > 1) {
        tempCart[cartIndex] = {
          ...tempCart[cartIndex],
          qtyInCart:
            tempCart[cartIndex].qtyInCart > 1
              ? tempCart[cartIndex].qtyInCart - 1
              : tempCart[cartIndex].qtyInCart,
        };
        setCart(tempCart);

        // restore qty in database
        let index = sessionData.findIndex((item) => item.id === id);
        setSessionData((prevValue) => {
          prevValue[index].quantity = prevValue[index].quantity + 1;
          return [...prevValue];
        });
      } else
        setShowError((prevVal) => {
          return {
            ...prevVal,
            status: "block",
            errorMSG: "Minimum quantity required : 1",
          };
        });
    }
  };

  const removeItem = (id) => {
    // restore qty in database
    let index = sessionData.findIndex((item) => item.id === id);
    let cartIndex = cart.findIndex((item) => item.id === id);

    setSessionData((prevValue) => {
      prevValue[index].quantity =
        prevValue[index].quantity + cart[cartIndex].qtyInCart;
      return [...prevValue];
    });

    setCart((prevVal) => {
      return [...cart.slice(0, cartIndex), ...cart.slice(cartIndex + 1)];
    });
  };

  ///////////////////////////////////////////////////////////
  // place order
  // Update quantity in database
  // Empty the cart
  ///////////////////////////////////////////////////////////
  const placeOrder = () => {
    setData(sessionData);
    setCart([]);
  };

  // add pop effect to cart icon when ever item is added in cart
  function blow(){
    let cartIcon = document.querySelector(".cart_icon_container > .icon");
    cartIcon.classList.add("blow");
    setTimeout(()=> cartIcon.classList.remove("blow"),400);
  }

  //   props to be passed using context =====================================================
  const value = {
    data,
    cart,
    setCart,
    addToCart,
    reduceCartQty,
    removeItem,
    dataToDisplay,
    handleSearch,
    filters,
    setFilters,
    colors,
    genders,
    types,
    isFilterReset,
    setIsFilterReset,
    showError,
    setShowError,
    placeOrder,
  };

  return <context.Provider value={value}>{props.children}</context.Provider>;
};

export { context };
export default ShoppingContext;
