import React, { useContext } from "react";
import { Link } from "react-router-dom";
import Navbar from "../../Components/navbar/Navbar";
import "./success.scss";
import {context} from '../../ShoppingContext'
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';

const Success = () => {

  const {cart} = useContext(context);


  return (
    <div className="success_container">
      <Navbar />
      <section className="success_main">
        <div className="main_container">
          <CheckCircleOutlineIcon className="success_icon"/>
          <h1>Your Order is Successfully placed</h1>
          <p>Your order will reach you by <b>29 June 9:00PM IST</b></p>
          <div className="order_progress_container">
            <div className="progress ">
              <div className="circle success">
                <div className="inner_dot">

                </div>
              </div>
              Order Placed
            </div>
            <hr />
            <div className="progress">
              <div className="circle">
                <div className="inner_dot">

                </div>
              </div>
              Packed
            </div>
            <hr />
            <div className="progress">
              <div className="circle">
                <div className="inner_dot">

                </div>
              </div>
              In Transit
            </div>
            <hr />
            <div className="progress">
              <div className="circle">
                <div className="inner_dot">

                </div>
              </div>
              Delivered
            </div>
          </div>
          <Link to={"/"} className="shopmore_btn">
            <button className="shop_more_btn">Shop More</button>
          </Link>
        </div>
      </section>
    </div>
  );
};

export default Success;
