import React from "react";

const SummaryItem = (props) => {
  const {name, price, qtyInCart} = props.item;
  return (
    <div className="summary_item">
      <p>
        {name} <span>(Qty : {qtyInCart}) </span>
      </p>
      <h3 className="amount">{price*qtyInCart}</h3>
    </div>
  );
};

export default SummaryItem;
