import React, { useContext, useState } from "react";
import "./filter.scss";
import { context } from "../../ShoppingContext";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import CloseIcon from "@mui/icons-material/Close";

const Filter = () => {
  const {
    data,
    colors,
    genders,
    types,
    isFilterReset,
    setIsFilterReset,
    filters,
    setFilters,
  } = useContext(context);

  const [tempFilters, setTempFilters] = useState({
    color: [],
    type: [],
    gender: [],
    priceRange: [0, 10000],
  });

  const [isFilterOpen, setIsFilterOpen] = useState(false);

  function handleFilterSelect(name, val) {
    ////////////////////////////////////////////////////////////////////////////
    // first check for price filter
    // update min and max price accordingly
    ///////////////////////////////////////////////////////////////////////////
    if (name.includes("price")) {
      setTempFilters((prevVal) => {
        return {
          ...prevVal,
          priceRange: name.includes("min")
            ? [val, prevVal.priceRange[1]]
            : [prevVal.priceRange[0], val],
        };
      });

      // handle color, gender and type filter.================================
    } else {
      setTempFilters((prevVal) => {
        return {
          ...prevVal,
          [name]: prevVal[name].length //check is fliter array is empty
            ? prevVal[name].includes(val) //check if that filetr is already selected, if yes, on clicking twice that value should be removed
              ? [
                  ...prevVal[name].slice(0, prevVal[name].indexOf(val)),
                  ...prevVal[name].slice(prevVal[name].indexOf(val) + 1),
                ]
              : [...prevVal[name], val]
            : [val],
        };
      });
    }

    // Adds border to selected color filter.================================

    if (name === "color") {
      document
        .querySelector(`div.color[title=${val}]`)
        .classList.toggle("selected");
    }
  }

  // handleFilter -> execute to apply the filter.================================
  const handleFilter = (e) => {
    e.preventDefault();
    setIsFilterReset(false);
    setFilters(tempFilters);
  };

  // handleReset -> execute to reset the filter

  const handleReset = (e) => {
    setIsFilterReset(true); //this state is used for detecting whether useffect is invoked dues to apply filter or reset filter
    setTempFilters({
      color: [],
      type: [],
      gender: [],
      priceRange: [0, 10000],
    });
    setFilters({
      color: [],
      type: [],
      gender: [],
      priceRange: [0, 10000],
    });
    Array.from(document.querySelectorAll("div.color")).forEach((a) => {
      if ([...a.classList].includes("selected")) a.classList.remove("selected");
    });
  };

  function toggleFilter(){
    setIsFilterOpen(!isFilterOpen);
    document.querySelector(".filter_container").classList.toggle("active");
  }

  return (
    <aside className="filter_container">
      <h3>Refine by</h3>
      <form onSubmit={handleFilter} onReset={handleReset}>
        <div className="filter_name">
          <p className="title">Gender</p>
          {genders.map((gender) => (
            <div key={gender} className="filter_item">
              <label htmlFor={gender}>
                <input
                  type="checkbox"
                  onChange={() => handleFilterSelect("gender", gender)}
                />
              </label>
              <p>{gender}</p>
            </div>
          ))}
        </div>
        <div className="filter_name">
          <p className="title">T-Shirt Type</p>
          {types.map((type) => (
            <div key={type} className="filter_item">
              <label htmlFor={type}>
                <input
                  type="checkbox"
                  onChange={() => handleFilterSelect("type", type)}
                />
              </label>
              <p>{type}</p>
            </div>
          ))}
        </div>
        <div className="filter_name">
          <p className="title">Color</p>
          <div className="colors_container">
            {colors.map((color) => (
              <div
                key={color}
                title={color}
                className="color"
                style={{ backgroundColor: color }}
                onClick={() => handleFilterSelect("color", color)}
              ></div>
            ))}
          </div>
        </div>
        <div className="filter_name">
          <p className="title">Price Range</p>
          <div className="filter_item">
            <input
              name="min_price"
              type="number"
              placeholder="From"
              onChange={(e) =>
                handleFilterSelect(e.target.name, Number(e.target.value))
              }
            />{" "}
            :
            <input
              name="max_price"
              type="number"
              placeholder="To"
              onChange={(e) =>
                handleFilterSelect(e.target.name, Number(e.target.value))
              }
            />
          </div>
        </div>
        <div className="filter_btn_container">
          <button type="reset" className="btn">
            Reset
          </button>
          <button type="submit" className="btn">
            Apply Filter
          </button>
        </div>
      </form>
      <div className="burger_container">
        {!isFilterOpen && <FilterAltIcon fontSize="large" className="filter_icon" onClick={()=> toggleFilter()}/>}
        {isFilterOpen && <CloseIcon fontSize="large" className="filter_icon" onClick={()=> toggleFilter()}/>}
      </div>
    </aside>
  );
};

export default Filter;
