import Navbar from "../../Components/navbar/Navbar";
import "./cart.scss";
import CartItem from "../../Components/cartItem/CartItem";
import PriceDetails from "../../Components/priceDetails/PriceDetails";
import { useContext } from "react";
import {context} from '../../ShoppingContext'
import { Link, BrowserRouter } from "react-router-dom";
import SentimentVeryDissatisfiedIcon from '@mui/icons-material/SentimentVeryDissatisfied';
import Error from '../../Components/error/Error';


const Cart = () => {
  const {cart} = useContext(context);

  return (
    <div className="carts_container">
      <Navbar />
      <section className="cart_main">
        {cart.length ? (<div className="cart_summary">
          <h2 className="cart_title">Cart Summary</h2>
          <div className="cart_items_container">
            {cart.map((cartItem)=> <CartItem key={`cartItem-${cartItem.id}`} item = {cartItem}/>)}           
            
          </div>
        </div>) : <div className="empty_cart">
        <SentimentVeryDissatisfiedIcon className="sad_icon"/>
          <h2> No items added in cart.  </h2>
          <h3>Please browse items on homepage, we are sure you'd like many.</h3>
          <Link to={'/'}><button>Go to Home</button></Link>
          </div>
        }
        {cart.length > 0 && <PriceDetails />}
      </section>
      <Error />
    </div>
  );
};

export default Cart;
